import { useEffect } from 'react';
import Head from 'next/head';
import Link from 'next/link';

import ActiveLink from '../components/active-link/active-link.component';

import '../styles/sass/main.scss';
import './_app.scss';

const THEME_TYPES = {
  DEFAULT: 'app--default'
}

function MyApp({ Component, pageProps }) {
  const theme = THEME_TYPES.DEFAULT;

  useEffect(() => {
    window.dataLayer = window.dataLayer || [];
    function gtag(){window.dataLayer.push(arguments);}
    gtag('js', new Date());
    gtag('config', 'UA-166884867-1');
  }, []);

  return (
    <span className={theme}>
      <Head>
        <title>jwango</title>
        <script async src="https://www.googletagmanager.com/gtag/js?id=UA-166884867-1"></script>
      </Head>
      <div className='container column'>
        <header className='main__header'>
          <div className='content-wrapper'>
            <h1><img src="/brand_96.png" className="header__logo" alt="JW brand"/>ANGO</h1>
          </div>
        </header>
        <nav>
          <div className='content-wrapper'>
            <ActiveLink href='/' aliases={['/posts']} activeClassName='nav-link--active' wrapAsLink={false}>Blog</ActiveLink>
            <ActiveLink href='/tags' activeClassName='nav-link--active'>Tags</ActiveLink>
            <ActiveLink href='/solutions' activeClassName='nav-link--active'>Solutions</ActiveLink>
            <ActiveLink href='/about' activeClassName='nav-link--active'>About</ActiveLink>
          </div>
        </nav>

        <main className='content-wrapper'><Component {...pageProps} /></main>

        <hr className='content-wrapper'/>
        <footer className='main__footer content-wrapper'>
          <p>
            <a href='https://github.com/jwango/blog-react' target='_blank'><img src='/GitHub-Mark-120px-plus.png' alt='github icon' className='main__footer-icon'></img>source code</a>
            <a href='/rss.xml' target='_blank'><img src='/rss-icon.png' alt='rss icon' className='main__footer-icon'></img>subscribe</a>
          </p>
          <p>
            Got thoughts or questions? Tell <Link href='/about'><a>me</a></Link> • Icons powered by <a href='https://fontawesome.com/license' target='_blank'>Font Awesome</a> • &copy; 2020 
          </p>
        </footer>
      </div>
    </span>
  );
}

export default MyApp;
